<template>
  <div class="evaluate">
    <section v-if="openProject">
      <h2>{{projectTitle}}</h2>
      <div class="satisfactionArticle">
        <div class="noBox" @click="satisfaction = 1">
          <img v-if="satisfaction === 1" class="likeIcon" src="@/assets/images/icon_dissatisfied_h.png">
          <img v-else class="likeIcon" src="@/assets/images/icon_dissatisfied_n.png">
          <span :class="['likeText',satisfaction === 1 ? 'likeActive' : '']">不满意</span>
        </div>
        <div class="yesBox" @click="satisfaction = 2">
          <img v-if="satisfaction === 2" class="likeIcon" src="@/assets/images/icon_satisfied_h.png">
          <img v-else class="likeIcon" src="@/assets/images/icon_satisfied_n.png">
          <span :class="['likeText',satisfaction === 2 ? 'likeActive' : '']">满意</span>
        </div>
      </div>

      <div class="labelBoxArticle">
        <span :class="['labelBoxText',item.number ? 'labelBoxActive' : '']" @click="changeLabel(item.id)" v-for="item in detailsOutputList" v-if="item.evaluateType === 3 && item.evaluateItemType == 1 && (item.evaluateGroup == satisfaction || item.evaluateGroup == 0) && satisfaction !== 0">{{item.items}}</span>
      </div>

      <textarea v-if="openComment" placeholder="请输入评价，不超过100个字" v-model="activeComment" maxlength="100"></textarea>
    </section>

    <section>
      <h2>服务评价</h2>
      <div class="starBox">
        <img v-for="i in serviceEvaluateStar" class="starIcon" @click="serviceEvaluateStar = i" src="@/assets/images/icon_star_h.png">
        <img v-for="i in 5 - serviceEvaluateStar" class="starIcon" @click="serviceEvaluateStar = i + serviceEvaluateStar" src="@/assets/images/icon_star_n.png">
      </div>

      <span class="fiveStari">{{serviceEvaluateStar | starText}}</span>

      <div class="labelBoxArticle">
        <span :class="['labelBoxText',item.number ? 'labelBoxActive' : '']" @click="changeLabel(item.id)" v-for="item in detailsOutputList" v-if="item.evaluateType === 3 && item.evaluateItemType == 2 && (item.evaluateGroup == serviceEvaluateStar || item.evaluateGroup == 0) && serviceEvaluateStar !== 0">{{item.items}}</span>
      </div>

      <textarea class="details" placeholder="请输入评价，不超过100个字" v-model="serviceComment" maxlength="100"></textarea>
    </section>

    <div class="bottomButton">
      <p v-if="((openProject && satisfaction) || !openProject) && serviceEvaluateStar" class='bottomButtonActive' @click="submit">提交</p>
      <p v-else class='cantGo'>{{cantEvaluate}}</p>
    </div>
  </div>
</template>

<script>
  import {
    getEvaluateByEvaluatedId,
    getEvaluateList,
    saveEvaluate,
    IBPsaveEvaluate,
    updateComplaint
  } from "../service/getData";

  export default {
    name: 'evaluate',
    data() {
      return {
        id: '',
        beEvaluatedId: '',
        marketId: '',
        openComment: '', //1需要评语0不需要
        detailsOutputList: '',
        radioStatus: 1,
        activeComment: '',
        serviceComment: '',
        come: '', //1活动//2物业报修//3会议室//4投诉//5访客
        projectTitle: '',
        openProject: 0, //活动评价是否开启
        serviceEvaluateStar: 0,
        satisfaction: 0,
        cantEvaluate: '请选取产品评价'
      }
    },
    created() {
      this.getEvaluateByEvaluatedId()
    },
    methods: {
      getEvaluateByEvaluatedId: function() {
        getEvaluateByEvaluatedId(this.$route.params.id, this.$route.params.type).then(res => {
          if (res.res.code === 10000) {

            this.cityId = res.body.cityId
            this.isEvaluate = res.body.isEvaluate
            this.marketId = res.body.marketId
            this.userName = res.body.userName
            this.referenceNo = res.body.referenceNo
            this.referenceName = res.body.referenceName
            if (res.body.isEvaluate == 1) {
              this.$router.push({
                path: '/evaluateSuccess/0'
              })
            } else {
              this.getEvaluateList(res.body.marketId, res.body.cityId, this.$route.params.type)
            }
          }
        })
      },
      getEvaluateList: function(marketId, cityId, type) {
        getEvaluateList(marketId, cityId, type).then(res => {
          if (res.res.code === 10000) {
            this.id = res.body.template.id
            this.openProject = res.body.template.openProject
            this.projectTitle = res.body.template.projectTitle
            this.openComment = res.body.template.openComment
            this.detailsOutputList = res.body.template.detailsOutputList
            if (!this.openComment || !this.openProject) {
              this.cantEvaluate = '请选取服务评价'
            } else {
              this.cantEvaluate = '请选取' + this.projectTitle
            }
          }
        })
      },
      changeLabel: function(id) {
        let detailsOutputList = this.detailsOutputList
        for (var i = detailsOutputList.length; i--;) {
          if (detailsOutputList[i].id === id) {
            if (detailsOutputList[i].number) {
              detailsOutputList[i].number = 0
            } else {
              detailsOutputList[i].number = 1
            }
          }
        }
        this.detailsOutputList = detailsOutputList
      },
      submit: function() {
        let templateList = [],
          detailsOutputList = this.detailsOutputList,
          comment = '';
        for (let item of detailsOutputList) {
          if (item.number !== 0) {
            templateList.push(item)
          } else if (item.evaluateType == 2 && item.evaluateItemType == 2 && this.serviceEvaluateStar !== 0) {
            item.number = this.serviceEvaluateStar
            templateList.push(item)
          } else if (item.evaluateType == 1 && item.evaluateItemType == 1 && this.satisfaction !== 0) {
            item.number = this.satisfaction
            templateList.push(item)
          }
        }
        if (this.activeComment) {
          comment = comment + ",'projectComment':" + this.activeComment
        }
        if (this.serviceComment) {
          comment = comment + ",'comment':" + this.serviceComment
        }

        saveEvaluate(this.cityId, this.marketId, this.id, this.$route.params.id, JSON.stringify(templateList), this.userName, this.referenceNo, this.referenceName, comment).then(res => {
          if (res.res.code === 10000) {
            this.$router.push({
              path: '/evaluateSuccess/1'
            })
          }
        })
        let evaluate = {
          "userName": this.userName,
          "evaluateTime": (new Date()).valueOf(),
          "detailsOutputList": templateList
        }
        if (this.activeComment) {
          evaluate.projectComment = this.activeComment
        }
        if (this.serviceComment) {
          evaluate.serviceComment = this.serviceComment
        }
        if (this.openProject) {
          evaluate.projectTitle = this.projectTitle
        }
        if (this.$route.params.type == 2) {
          IBPsaveEvaluate(this.referenceNo, evaluate).then(res => {
            console.log(res)
          })
        } else if (this.$route.params.type == 4) {
          updateComplaint(this.marketId, this.referenceNo, evaluate).then(res => {
            console.log(res);
          })
        }
      }
    },
    filters: {
      starText: function(value) {
        switch (value) {
          case 1:
            return '非常差'
            break;
          case 2:
            return '差'
            break;
          case 3:
            return '一般'
            break;
          case 4:
            return '满意'
            break;
          case 5:
            return '非常满意'
            break;
          default:

        }
      }
    },
    watch: {
      serviceEvaluateStar: function() {
        if (this.openProject && !this.satisfaction) {
          this.cantEvaluate = '请选取' + this.projectTitle
        }
        let detailsOutputList = this.detailsOutputList
        for (let item of detailsOutputList) {
          if (item.evaluateType == 3 && item.evaluateItemType == 2) {
            item.number = 0
          }
        }
        this.detailsOutputList = detailsOutputList
      },
      satisfaction: function() {
        if (!this.serviceEvaluateStar) {
          this.cantEvaluate = '请选取服务评价'
        }
        let detailsOutputList = this.detailsOutputList
        for (let item of detailsOutputList) {
          if (item.evaluateType == 3 && item.evaluateItemType == 1) {
            item.number = 0
          }
        }
        this.detailsOutputList = detailsOutputList
      }
    }
  }
</script>

