<template>
  <div class="questionnaire">
    <header>
      <img src="@/assets/images/vlogo.png" alt="">
      <h2>万科产办2018客户满意度调研</h2>
    </header>
    <section v-for="(question,ind) in questions">
      <div class="title">{{question.caption}}</div>
      <div class="optionBox" v-for="(item,index) in question.options" @click="activeOptions(ind,index)" v-if="question.type === '1'">
        <div class="itemActiveButtom" v-if="question.isDrop === item.id">
          <i></i>
        </div>
        <div v-else class="itemButtom"></div>
        <p :style="{color: question.isDrop === item.id ? 'rgba(51,51,51,1)' : 'rgba(153, 153, 153, 1)'}">{{item.descr}}</p>
      </div>
      <textarea v-if="question.type === '4'" name="name" v-model="question.isDrop" placeholder="文字简要描述" @change="changeFlag" maxlength="200"></textarea>
    </section>
    <div class="buttonBox">
      <p v-if="flag" @click="addQtAnswer" class="submit">提交问卷</p>
      <p v-else>提交问卷</p>
    </div>
  </div>
</template>

<script>
  import {
    getNaireDetail,
    addQtAnswer
  } from "../service/getData";

  export default {
    name: 'detail',
    data() {
      return {
        questions: [],
        flag: false
      }
    },
    created() {
      this.getNaireDetail()
    },
    methods: {
      getNaireDetail: function() {
        getNaireDetail(this.$route.params.id, this.$route.params.userId).then(res => {
          if (res.res.code === 10000) {
            if (res.body.qtQuestionnaire) {
              let questions = res.body.qtQuestionnaire.questions
              for (var index in questions) {
                if (questions[index].type === '4') {
                  questions[index].isDrop = ''
                }
              }
              this.questions = questions;
              console.log(questions);
            } else {
              this.$router.push({
                path: '/evaluateSuccess/2'
              })
            }
          }
        })
      },
      addQtAnswer: function() {
        let params = [],
          questions = this.questions;
        for (var i = 0; i < questions.length; i++) {
          params[i] = {}
          if (questions[i].type === '1') {
            params[i].userId = this.$route.params.userId
            params[i].naireId = this.$route.params.id
            params[i].questionId = questions[i].id
            params[i].optionId = questions[i].isDrop
          } else {
            params[i].userId = this.$route.params.userId
            params[i].naireId = this.$route.params.id
            params[i].questionId = questions[i].id
            params[i].content = questions[i].isDrop
          }
        }
        addQtAnswer(params, this.$route.params.id, this.$route.params.userId).then(res => {
          if (res.res.code === 10000) {
            this.$router.push({
              path: '/evaluateSuccess/2'
            })
          }
        })
      },
      changeFlag: function() {
        let flag = true
        for (var index in this.questions) {
          if (this.questions[index].isDrop === 0 && this.questions[index].type === '1' || this.questions[index].isDrop === '' && this.questions[index].type === '1') {
            flag = false
          }
        }
        this.flag = flag
      },
      activeOptions: function(ind, index) {
        this.questions[ind].isDrop = this.questions[ind].options[index].id
        this.changeFlag()
      }
    }
  }
</script>

